import React from 'react';

import './Offer.css';

const range1 = [
  'wodno-kanalizacyjne',
  'gazowe',
  'centralnego ogrzewania'
];

const range2 = [
  'kanalizacyjne',
  'wodocigowe',
  'gazowe'
];

const Offer = () => (
  <div className="offer">
    <h3>Oferta</h3>
    <div>
      <p>
        Nasz specjalnoci s instalacje wewntrzne:
      </p>
      <div className="offer__list">
        {range1.map((title, key) => (
          <div
            key={key}
            className="offer__list-item"
          >
            {title}
          </div>
        ))}
      </div>
      <p>
        a take sieci zewntrzne:
      </p>
      <div className="offer__list">
        {range2.map((title, key) => (
          <div
            key={key}
            className="offer__list-item"
          >
            {title}
          </div>
        ))}
      </div>
      <hr />
      <p>
        Wykonujemy instalacje klimatyzacji i wentylacji oraz monta pomp ciepa i kolektorw sonecznych. Realizujemy take roboty towarzyszce, takie jak odtworzenie tynkw, zabudowa gips-karton, malowanie oraz ukadanie kostki brukowej.
      </p>
    </div>
  </div>
);

export default Offer;



