import React from 'react';

import Offer from '../Offer';
import Gallery from '../Gallery';

import "./AboutUs.css";

const AboutUs = () => (
  <div className="about-us">
    <h3>Firma</h3>
    <div className="about-us__text">
        Firma Hydrobud powstaa w 1992 roku pod nazw Zakad Instalacji Sanitarnych C.O. i Gaz. W 2002 roku nastpia zmiana nazwy na obecn. W cigu 25 lat dziaalnoci zrealizowalimy szereg inwestycji dla klientw instytucjonalnych i indywidualnych na terenie caego kraju. Specjalizujemy si w wykonawstwie instalacji hydraulicznych, wodno-kanalizacyjnych, centralnego ogrzewania, wentylacji i klimatyzacji. Posiadamy kompetentn kadr oraz niezbdny sprzt do wykonywania powierzonych prac. Priorytetem firmy jest maksymalizacja satysfakcji klientw.
    </div>
    <hr />
    <Gallery />
    <Offer />
  </div>
);

export default AboutUs;



