import React from 'react';

import "./Realisations.css";

const realisations = [
  "Wykonanie instalacji wentylacji, klimatyzacji, ogrzewania i kanalizacji sanitarnej wraz z odtworzeniem nawierzchni drogowej przy placu wglowym w Elektrociepowni Kozienice.",
  "Wykonanie instalacji wodocigowej, gazowej i sanitarnej wraz ze zbiornikiem retencyjnym w ramach rozbudowy zakadu Ajimoto w Kakowie.",
  "Monta instalacji wentylacji mechanicznej, chodniczej, grzewczej, kanalizacji sanitarnej i klimatyzacji w Zespole budynkw biurowych Libra ul. Daimlera 2 w Warszawie.",
  "Wykonanie instalacji centralnego ogrzewania, wodno  kanalizacyjnej, gazu i kotowni w Zespole budynkw mieszkalnych wielorodzinnych z garaami podziemnymi Wilanw ul. Bruzdowa w Warszawie.",
  "Monta instalacji sanitarnej, centralnego ogrzewania i solarnej na budowie Aquapark w Siedlcach.",
  "Wykonanie instalacji wody, kanalizacji sanitarnej i deszczowej, centralnego ogrzewania, ciepa technologicznego wraz z kotowni, wentylacji mechanicznej, spronego powietrza w zakadzie artykuw higieniczno  kosmetycznych w Nowym Dworze Mazowieckim.",
  "Wymiana instalacji wod-kan, centralnego ogrzewania, monta wza cieplnego i instalacji p.po wraz z instalacj elektryczn w Centrum Medycznym Ksztacenia Podyplomowego przy ul. Marymonckiej w Warszawie.",
  "Wykonanie instalacji centralnego ogrzewania, zimnej i ciepej wody uytkowej oraz instalacji p.po. w internacie Wojskowej Agencji Mieszkaniowej przy ul. Zegrzyskiej 17 w Legionowie.",
  "Budowa instalacji centralnego ogrzewania, wodocigowej, hydrantowej, kanalizacji wraz z kolektorami w ramach rewitalizacji zespou biurowego Cross Point w odzi.",
  "Wykonanie zewntrznych sieci: wodocigowej, kanalizacji deszczowej i sanitarnej dla budynkw przy ul. Okunin 134 w Nowym Dworze Mazowieckim.",
  "Remont instalacji sanitarnych i grzewczych na budowie WB Electronics w Oarowie Mazowieckim.",
  "Przeprowadzenie kompletnego remontu instalacji sanitarnych i grzewczych w ocynkowni w Dbicy.",
  "Wykonanie kompletu instalacji sanitarnych i grzewczych na budowie Fabryki Wanien Akrylowych i Kabin Prysznicowych CERSANIT II i na budowie Fabryki Mebli azienkowych Cersanit II w Starachowicach.",
];

const Realisations = () => (
  <div className="content__overlay realisations">
    <h3>Realizacje</h3>
    <strong>Przykady niektrych z naszych realizacji:</strong>
    <div className="realisations__list">
      {realisations.map((realisation, key) => (
        <span key={key}>{realisation}</span>
      ))}
    </div>
    <hr />
    <h3>Referencje</h3>
    <div className="realisations__references">
        Referencje przedstawiamy na yczenie.
    </div>
  </div>
);

export default Realisations;



